.input-box {
	display: flex;
	flex-direction: column;
	width: 20%;
	margin: 2rem auto;
}

.input-box div {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 5px;
}

button {
	width: 5rem;
	height: 2rem;
	align-self: center;
	background-color: #303841;
	border: none;
	color: white;
	cursor: pointer;
	border-radius: 2px;
}

button.active {
	background-color: white;
	border: 1px solid #303841;
	color: #303841;
}

@media only screen and (max-width: 800px) {
	.input-box {
		width: 80%;
	}
}